<template>
  <div class="tab">
    <div class="title">
      <a href="javascript:" class="abtn" :class="{'active': showNot}" @click="toggleShowNot(true)">未参与 ({{count.notJoin}})</a>
      <a href="javascript:" class="abtn" :class="{'active': !showNot}" @click="toggleShowNot(false)">已参与 ({{count.hadJoin}})</a>
    </div>
    <div class="lists">
      <scroll class="not scroll-wrapper"
              v-show="showNot"
              :data="joinMember.notJoinList"
              :pullup="true"
              @scrollToEnd="getMoreNotJoin"
      >
        <div class="scroll-content" v-show="count.notJoin">
          <ul class="list">
            <li class="item" v-for="(item, index) in joinMember.notJoinList" :key="index">
              <img class="avatar" :src="item.avatar || defaultAvatar" alt="">
              <div class="des">
                <div class="top">
                  <span class="name">{{item.name}}</span>
                </div>
              </div>
            </li>
          </ul>
          <slot name="not-join"></slot>
        </div>
        <p class="tips" v-if="!count.notJoin">暂无成员</p>
      </scroll>
      <scroll class="had scroll-wrapper"
              v-show="!showNot"
              :data="joinMember.hadJoinList"
              :pullup="true"
              @scrollToEnd="getMoreHadJoin"
      >
        <div class="scroll-content" v-show="count.hadJoin">
          <ul class="list">
            <li class="item" v-for="(item, index) in joinMember.hadJoinList" :key="index">
              <img class="avatar" :src="item.avatar || defaultAvatar" alt="">
              <div class="des">
                <div class="top">
                  <span class="name">{{item.name}}</span>
                  <span class="time">{{item.time}}</span>
                </div>
                <div class="bottom" v-if="pageType === 'vote'">
                  已投选项“<ins class="highlight">{{item.answer}}</ins>”
                </div>
              </div>
            </li>
          </ul>
          <slot name="had-join"></slot>
        </div>
        <p class="tips" v-if="!count.hadJoin">暂无成员</p>
      </scroll>
    </div>
    <div class="btn" v-show="showNot">
      <button @click="remind">{{count.canRemind ? '一键提醒' : '已提醒'}}</button>
    </div>
  </div>
</template>

<script>
import avatar from '@/common/images/default-avatar.png';

import Scroll from '@/base/scroll/scroll';

export default {
  props: {
    // 标记是调研还是统计
    pageType: {
      type: String,
      default: 'vote',
      required: true
    },
    // 参与成员
    count: {
      type: Object,
      required: true
    },
    joinMember: {
      type: Object,
      required: true
    }

  },
  data() {
    return {
      showNot: true, // 是否显示未参与
      defaultAvatar: avatar // 默认头像
    };
  },
  methods: {
    // 加载更多已参与成员
    getMoreHadJoin() {
      this.$emit('getMoreHadJoin');
    },
    // 加载更多未参与成员
    getMoreNotJoin() {
      this.$emit('getMoreNotJoin');
    },
    // 提醒参与
    remind() {
      this.$emit('remind');
    },
    // 切换参与未参与列表
    toggleShowNot(flag) {
      this.showNot = flag;
      this.$emit('changeShowNot', flag);
    }
  },
  components: {
    Scroll
  }
};
</script>
<style lang="stylus" scoped>
@import "~@/common/stylus/variable"

.tab
  display flex
  flex-direction column
  height 100%
  background-color #f4f4f4
  font-size 0
  .title
    flex-shrink 0
    display flex
    align-items center
    position relative
    z-index 10
    border-bottom 0.01rem solid #888
    background-color #fff
    font-size 0.4rem
    .abtn
      width 50%
      height 0.85rem
      line-height 0.8rem
      border-bottom 0.05rem solid transparent
      text-align center
      color #212121
      &.active
        color $color-main
        border-color $color-main
  .lists
    flex-grow 1
    .scroll-wrapper
      position absolute
      top 0.85rem
      width 100%
      overflow hidden
      .scroll-content
        min-height 101%
        .list
          min-height 101%
      .tips
        line-height 2rem
        text-align center
        font-size 0.36rem
        color #777
      &.not
        bottom 1.4rem
      &.had
        bottom 0
      .list
        padding 0 0.25rem
        background-color #fff
        .item
          display flex
          align-items center
          padding 0.1rem 0.05rem
          border-bottom 0.01rem solid #eee
          .avatar
            border-radius 50%
            background-color #aaa
            width 1rem
            height 1rem
            margin-right 0.3rem
          .des
            flex-grow 1
            display flex
            flex-direction column
            .top
              display flex
              flex-direction row
              justify-content space-between
              align-items center
              .name
                font-size 0.38rem
              .time
                flex-grow 1
                align-self flex-start
                margin-top 0.1rem
                text-align right
                font-size 0.34rem
                color #777
            .bottom
              font-size 0.34rem
              text-align justify
              .highlight
                text-decoration none
                color $color-main

  .btn
    position fixed
    bottom 0
    width 100%
    padding 0.3rem 0
    text-align center
    background-color #f4f4f4
    button
      width 4rem
      height 0.8rem
      border 0.01rem solid $color-main
      border-radius 0.08rem
      background-color transparent
      color $color-main
      font-size 0.42rem
</style>




