<template>
  <div class="survey-detail" v-show="ready">
    <div class="survey-wrapper" :class="{'limit-height': showJoinMember && !isPreview && isAdmin}">
      <!-- survey detail panel -->
      <panel page-type="survey"
            :data="detail"
            :status="statusText"
            :is-admin="isAdmin"
      />

      <!-- survey form -->
      <form class="question-form"
            @submit.prevent
            ref="form"
            novalidate
      >
        <!-- 问题列表 -->
        <ul class="question-list">
          <li class="item" v-for="(item, indexQ) in questionInof" :key="item.id">
            <!-- 问题标题 -->
            <p class="title" :class="{'required': +item.required}">
              <span class="title-text">{{`${indexQ + 1}、${item.title}`}}</span>
              <span class="title-tag">{{questionTag(item)}}</span>
            </p>
            <!-- 选择题 -->
            <ul class="options" v-if="item.type < 3">
              <li class="option" v-for="(opt, indexO) in item.option" :key="indexO">
                <input
                  :type="questionType(item)"
                  :name="item.id"
                  :id="questionId(indexQ, indexO, item)"
                  :value="indexO + 1" :checked="opt.checked"
                  :disabled="isHadJoin"
                  :required="!!+item.required"
                  :least="item.least"
                  :most="item.most"
                >
                <label :for="questionId(indexQ, indexO, item)">
                  <span class="icon"></span>
                  <span>{{thumbs[indexO]}}、</span>{{opt.title}}</label>
              </li>
            </ul>
            <!-- 简答题(单行) -->
            <div class="txt" v-else-if="item.type == 3">
              <input type="text" :name="item.id" :value="item.value" :readonly="isHadJoin" :required="!!+item.required" />
            </div>
            <!-- 简答题(多行) -->
            <div class="txt" v-else>
              <textarea :name="item.id" :value="item.value" :readonly="isHadJoin" :required="!!+item.required"></textarea>
            </div>
          </li>
        </ul>
        <!-- 提交按钮 -->
        <div class="btn" v-if="!isAdmin && !isHadJoin || isPreview">
          <button type="submit" @click="checkForm">提交</button>
        </div>
      </form>
    </div>
    <!-- 查看详情按钮 -->
    <div class="collaps" ref="collaps" v-if="!isPreview && isAdmin">
      <button @click="collaps" :class="{'open': showJoinMember}">{{collapsText}}</button>
    </div>
    <!-- 参与人员列表 -->
    <div class="statistics" v-show="showJoinMember" v-if="!isPreview && isAdmin">
      <tab pageType="survey"
        ref="tab"
        :count="joinCount"
        :joinMember="joinMember"
        @remind="remind"
        @getMoreHadJoin="getMoreHadJoin"
        @getMoreNotJoin="getMoreNotJoin"
      >
        <load-more slot="had-join" :over="!hadJoin.hasMore" :is-loading="hadJoin.isLoading"/>
        <load-more slot="not-join" :over="!notJoin.hasMore" :is-loading="notJoin.isLoading"/>
      </tab>
    </div>
    <!-- 一键提醒 -->
    <div class="replacement" v-show="!showJoinMember" v-if="!isPreview && isAdmin">
      <button @click="remind">{{joinCount.canRemind ? '一键提醒' : '已提醒'}}</button>
    </div>
  </div>
</template>

<script type="ecmascript-6">
import { formData } from '@/common/js/dom';
import { SURVEY_STATE_TEXT, THUMBS } from '@/common/js/config';

import { getSurveyDetail, submitSurvey, getSurveyHadJoin, getSurveyNotJoin, remindSurvey } from '@/api/survey';
import { AID, ERR_OK, LOGIN_EXPIRE } from '@/api/config';

import Panel from '@/base/panel/vote-survey-detail';
import LoadMore from '@/base/load-more/load-more';
import Tab from '@/base/tab/join-member';

import { mapMutations, mapGetters } from 'vuex';


export default {
  data() {
    return {
      // 详情面板
      detail: {
        status: 0
      },
      // 调研问题
      questionInof: [],

      // 显示参与人员 -- 管理员
      showJoinMember: true,
      // 调研参与人数 -- 管理员
      joinCount: {
        notJoin: 0,
        hadJoin: 0,
        canRemind: false
      },
      // had join loading -- 管理员
      hadJoin: {
        hasMore: true,
        isLoading: false,
        limit: 10,
        offset: 0
      },
      // not join loading -- 管理员
      notJoin: {
        hasMore: true,
        isLoading: false,
        limit: 10,
        offset: 0
      },
      // 参与成员列表 -- 管理员
      joinMember: {
        hadJoinList: [],
        notJoinList: []
      },
      // 当前是否预览页 -- 管理员
      isPreview: false
    };
  },
  created() {
    // 判断是否是预览页
    if (this.$route.meta.preview) {
      this.isPreview = true;
    }
    // 数据就绪
    this.ready = false;
    // 调研状态
    this.statusText = SURVEY_STATE_TEXT;
    // 问题序号
    this.thumbs = THUMBS;
    // 折叠按钮文字 -- 管理员
    this.collapsText = '查看详情';
    // 请求调研详情
    this._getDetail();

    // 请求调研参与成员 -- 管理员
    if (this.isAdmin) {
      this._getHadJoin();
      this._getNotJoin();
    }
  },
  computed: {
    // 已参与标记
    isHadJoin() {
      return Number(this.detail.status) === 3;
    },
    ...mapGetters(['isAdmin'])
  },
  methods: {
    // 获取详情
    _getDetail() {
      const surveyId = this.$route.params.id;

      getSurveyDetail({ id: surveyId }).then((res) => {
          if (res.data.code === ERR_OK) {
              this.detail = res.data.data.detail;

              this.questionInof = res.data.data.question_info;

              this.ready = true;

              // 详情接口后端未给出管理员身份标志，前端根据返回字段判断
              if ('unjoin_mem_count' in res.data.data && !this.isAdmin) {
                this.setUserType({ isAdmin: true });

                this._getHadJoin();

                this._getNotJoin();
              }

              if (!('unjoin_mem_count' in res.data.data) && this.isAdmin) {
                this.setUserType({ isAdmin: false });
              }


              if (this.isAdmin) {
                this.joinCount.canRemind = Number(this.detail.canRemind) === 1;

                this.joinCount.hadJoin = res.data.data.join_mem_count;

                this.joinCount.notJoin = res.data.data.unjoin_mem_count;
              }

          } else {
            const that = this;

            this.$dialog.alert({
              mes: res.data.msg,
              callback() {
                that.$router.push(`/survey/list/aid/${AID}/`);
              }
            });
          }

        }).catch((err) => {
          console.log(err);
        });
    },
    // 表单提交
    _submit(param) {
      if (this.submitThrottle) {
        return;
      }
      this.submitThrottle = true;

      const surveyId = this.$route.params.id;

      param = Object.assign({}, param, { id: surveyId });

      submitSurvey(param).then((res) => {
        const that = this;

        if (res.data.code === ERR_OK) {
          this.detail.status = 3;

          this.$dialog.alert({
            mes: res.data.data.finish,
            callback() {
              that.$router.push(`/survey/list/aid/${AID}/`);
            }
          });

          this.updateVoteStatus({ surveyId, status: this.detail.status });

        } else {
          const that = this;

          this.$dialog.alert({
            mes: res.data.msg,
            callback() {
              that.submitThrottle = false;
            }
          });
        }
      }).catch((err) => {
        console.error(err);
        this.submitThrottle = true;
      });
    },
    // 生成选项类别
    questionType(item) {
      const type = Number(item.type) === 1 ? 'radio' : 'checkbox';

      return type;
    },
    // 生成选项id
    questionId(index1, index2, item) {
      let prefix = '';
      switch (Number(item.type)) {
        case 1:
          prefix = 'radio';
          break;
        case 2:
          prefix = 'checkbox';
          break;
        default:
          prefix = 'short';
      }
      return prefix + index1 + index2;
    },
    // 题目标签(单选/多选)
    questionTag(item) {
      const type = Number(item.type);

      if (type === 1) {
        return `【单选】`;

      } else if (type === 2) {
        const limit = JSON.parse(item.extra);

        return `【多选】【可选择${limit.least}-${limit.most}项】`;

      } else {
        return `【简答】`;
      }
    },
    // 校验表单
    checkForm(form) {
      if (this.isPreview) {

        this.$dialog.alert({
          mes: '当前页面仅供预览！',
          callback() { }
        });

        return;
      }

      let tempData = formData(this.$refs.form);
      let errorKeys = Object.keys(tempData.errors);
      let answerKeys = Object.keys(tempData.data);

      if (errorKeys.length || !answerKeys.length) {
          this.$dialog.alert({
            mes: '您的答卷不完整！',
            callback() {
              window.scrollTo(0, tempData.errors[errorKeys[0]].offsetTop);
            }
          });

        return;

      } else {
        this._submit({ answer: tempData.data })
      }

    },

    // 获取已参与成员 -- 管理员
    _getHadJoin() {
      this.hadJoin.isLoading = true;

      const id = this.$route.params.id;
      const limit = this.hadJoin.limit;
      const offset = this.hadJoin.offset;

      getSurveyHadJoin({ id, limit, offset }).then((res) => {
        if (res.data.code === ERR_OK) {

          this.joinMember.hadJoinList.push(...res.data.data.mem_info);

          this.hadJoin.isLoading = false;

          if (res.data.data.mem_info.length < this.hadJoin.limit) {
            this.hadJoin.hasMore = false;
          }
        }
      }).catch((err) => {
        console.error(err);
      });
    },
    // 获取未参与成员 -- 管理员
    _getNotJoin() {
      this.notJoin.isLoading = true;

      const id = this.$route.params.id;
      const limit = this.notJoin.limit;
      const offset = this.notJoin.offset;

      getSurveyNotJoin({ id, limit, offset }).then((res) => {
        if (res.data.code === ERR_OK) {

          this.joinMember.notJoinList.push(...res.data.data.mem_info);

          this.notJoin.isLoading = false;

          if (res.data.data.mem_info.length < this.notJoin.limit) {
            this.notJoin.hasMore = false;
          }
        }
      }).catch((err) => {
        console.error(err);
      });
    },
    // 获取更多已参与人员 -- 管理员
    getMoreHadJoin() {
      if (!this.hadJoin.hasMore) {
        return
      }
      this.hadJoin.offset += 10;

      this._getHadJoin();
    },
    // 获取更多未参与人员 -- 管理员
    getMoreNotJoin() {
      if (!this.notJoin.hasMore) {
        return
      }
      this.notJoin.offset += 10;

      this._getNotJoin();
    },
    // 提醒参与 -- 管理员
    remind() {
      if (!this.joinCount.canRemind) {
        this.$dialog.toast({
            mes: '请稍后再来提醒吧！',
            timeout: 1500
        });
        return;
      }

      if (this.remidThrottle) {
        return;
      }
      this.remidThrottle = true;

      const surveyId = this.$route.params.id;

      remindSurvey({ id: surveyId }).then((res) => {
        if (res.data.code === ERR_OK) {

          this.$dialog.alert({
            mes: `提醒成功`,
            timeout: 1500
          });

          this.joinCount.canRemind = false;
        }

      }).catch((err) => {
        console.error(err);
        this.remidThrottle = false;
      });
    },
    // 展开/折叠详情
    collaps() {
      this.showJoinMember = !this.showJoinMember;

      if (this.showJoinMember) {
        this.collapsText = '收起';
        this.$refs.collaps.style = '';
      } else {
        this.collapsText = '查看详情';

        this.$refs.collaps.style.marginBottom = '1.4rem';
      }
    },
    ...mapMutations({
      updateVoteStatus: 'UPDATE_SURVEY_STATUS',
      setUserType: 'SET_USER_TYPE'
    })
  },
  components: {
    Panel,
    Tab,
    LoadMore
  }
};
</script>

<style lang="stylus" scoped>
@import "~@/common/stylus/variable";

.survey-detail
  -webkit-overflow-scrolling: touch;
  position absolute
  top 0
  bottom 0
  right 0
  left 0
  display flex
  flex-direction column
  &::-webkit-scrollbar
    display none
  .survey-wrapper.limit-height
    max-height 3.4rem
    overflow hidden
  .question-form
    font-size 0
    .question-list
      .item
        margin-top 0.2rem
        padding 0.2rem 0
        box-shadow 0 2px 8px 1px #ddd
        background-color #fff
        font-size 0.38rem
        .title
          padding-left 0.2rem
          font-size 0
          &.required::before
            content "*"
            color $color-main
            font-size 0.36rem
          .title-tag
            color $color-main
            font-size 0.36rem
          .title-text
            font-size 0.36rem
        .options
          input[type="radio"],
          input[type="checkbox"]
            display none
          input[type="radio"]+label>.icon
            background url("../../common/images/radio.png") no-repeat
            background-size cover
          input[type="checkbox"]+label>.icon
            background url("../../common/images/checkbox.png") no-repeat
            background-size cover
          input[type="checkbox"]:disabled+label>.icon
            background url("../../common/images/checkbox-disabled.png") center center no-repeat
            background-size cover
          input[type="radio"]:disabled+label>.icon
            background url("../../common/images/radio-disabled.png") center center no-repeat
            background-size cover
          input[type="checkbox"]:checked+label>.icon
            background url("../../common/images/checkbox-checked.png") center center no-repeat
            background-size cover
          input[type="radio"]:checked+label>.icon
            background url("../../common/images/radio-checked.png") center center no-repeat
            background-size cover
            background-clip border-box
          input[type="checkbox"]:checked+label
            background-color #f8ecec
          input[type="radio"]:checked+label
            background-color #f8ecec
          label
            display flex
            align-items center
            min-height 0.8rem
            font-size 0.36rem
            word-break break-all
            &>.icon
              flex-shrink 0
              width 0.4122rem
              height 0.4122rem
              margin 0 0.25rem 0 0.3rem
        .txt
          display flex
          padding 0.2rem 0.5rem
          textarea
            flex-grow 1
            height 2.3rem
            padding 0.05rem
            border 0.01rem solid #ccc
            resize none
            text-align justify
          input
            flex-grow 1
            height 0.6rem
            padding 0 0.05rem
            border 0.01rem solid #ccc
    .btn
      margin 0.5rem 0 0.8rem
      text-align center
      button
        width 4rem
        height 0.8rem
        border none
        border-radius 0.05rem
        background-color $color-main
        color #fff
        font-size 0.36rem
  .collaps
    margin-bottom 0.2rem
    padding 0.2rem 0
    background-color #fff
    box-shadow: 0 -0.2rem 0.5rem 0 #fff;
    text-align center
    button
      border none
      outline none
      font-size 0.36rem
      color #777
      &.open::after
        padding 0.1rem 0.3rem
        background url("../../assets/arrow-down.png") no-repeat center center
        background-size 0.3rem 0.192rem
      &::after
        display inline-block
        content " "
        padding 0.1rem 0.3rem
        background url("../../assets/arrow-up.png") no-repeat center top
        background-size 0.3rem 0.192rem
  .statistics
    flex-grow 1
    flex-shrink 0
    position relative
  .replacement
    position fixed
    bottom 0
    width 100%
    padding 0.3rem 0
    text-align center
    background-color #f4f4f4
    button
      width 4rem
      height 0.8rem
      border 0.01rem solid $color-main
      border-radius 0.08rem
      background-color transparent
      color $color-main
      font-size 0.42rem
</style>



